/******* Mixins *******/
/******* App Styles *******/
body {
  background-color: #f2f2f2;
  font-family: 'Open Sans', Arial, sans-serif; }

.weather-preloader {
  width: 500px;
  padding: 40px 20px;
  margin: 0 auto; }

.location {
  width: 500px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 10px auto 10px; }

.svg-icon-location {
  width: 1.5em;
  height: 1.5em;
  display: inline-block;
  padding: 10px 0; }

.svg-icon-location path,
.svg-icon-location polygon,
.svg-icon-location rect {
  fill: #9ca4a8; }

.svg-icon-location circle {
  stroke: #4691f6;
  stroke-width: 1; }

.city-weather {
  display: inline-block;
  padding: 10px;
  vertical-align: top; }

.dayWeekItem {
  background-color: white;
  -webkit-box-shadow: 0px 1px 4px #CCC;
  box-shadow: 0px 1px 4px #CCC;
  -webkit-background-clip: padding-box;
  -webkit-border-radius: 5px;
  -moz-background-clip: padding-box;
  border-radius: 5px;
  background-clip: padding-box;
  padding: 15px 20px;
  display: block;
  text-decoration: none;
  width: 500px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0 auto 15px;
  cursor: pointer; }
  .dayWeekItem:after {
    content: "";
    display: table;
    clear: both; }
  .dayWeekItem .top-content:after {
    content: "";
    display: table;
    clear: both; }
  .dayWeekItem .icon-weather, .dayWeekItem .date, .dayWeekItem .temperature {
    float: left;
    text-align: left;
    text-transform: uppercase; }
  .dayWeekItem .icon-weather {
    height: 90px;
    width: 45px;
    border-radius: 0 90px 90px 0;
    background-color: transparent;
    margin-left: -20px;
    margin-right: 30px; }
  .dayWeekItem .Clear {
    background-color: #fcba08; }
  .dayWeekItem .Rain {
    background-color: #29abe2; }
  .dayWeekItem .Clouds {
    background-color: #d2e5ec; }
  .dayWeekItem .date .weekday {
    font-size: 24px;
    font-weight: 400;
    color: #000; }
  .dayWeekItem .date .day-long {
    font-weight: 300;
    color: #808080; }
  .dayWeekItem .date .weather-description {
    color: #808080;
    font-weight: 300;
    background-color: #efefef;
    padding: 1px 8px;
    margin-top: 3px;
    text-align: center;
    font-size: 13px;
    display: inline-block; }
  .dayWeekItem .temperature {
    float: right;
    font-size: 30px; }
    .dayWeekItem .temperature .temp-high {
      font-weight: 800;
      color: #000; }
    .dayWeekItem .temperature .temp-low {
      font-weight: 300;
      color: #b3b3b3; }

.randomGif .randomGifImg {
  -webkit-clip-path: circle(100px at center);
          clip-path: circle(100px at center);
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-shape-outside: circle(70% at 0% 50%) border-box;
          shape-outside: circle(70% at 0% 50%) border-box; }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
.toggleContent-open {
  max-height: 200px;
  overflow: hidden;
  opacity: 1;
  display: block; }
  .toggleContent-open:after {
    content: "";
    display: table;
    clear: both; }

.toggleContent-closed {
  max-height: 0;
  opacity: 0;
  display: none; }
  .toggleContent-closed:after {
    content: "";
    display: table;
    clear: both; }



